import { EditorContent, useEditor } from '@tiptap/react';
import Document from '@tiptap/extension-document';
import Paragraph from '@tiptap/extension-paragraph';
import BulletList from '@tiptap/extension-bullet-list';
import ListItem from '@tiptap/extension-list-item';
import OrderedList from '@tiptap/extension-ordered-list';
import Bold from '@tiptap/extension-bold';
import Italic from '@tiptap/extension-italic';
import DropCursor from '@tiptap/extension-dropcursor';
import Text from '@tiptap/extension-text';
import Link from '@tiptap/extension-link';
import { useCallback, useEffect } from 'react';

export function RichTextView({ children, ...props }: React.PropsWithChildren<React.HTMLAttributes<HTMLElement>>) {
  const getContent = useCallback((children: any) => {
    let content;
    try {
      content = JSON.parse(children);
    } catch {
      content = children;
    }
    return content;
  }, [children]);

  const richTextView = useEditor({
    extensions: [
      BulletList.configure({ HTMLAttributes: { class: 'list-disc ml-5' } }),
      ListItem,
      OrderedList.configure({ HTMLAttributes: { class: 'list-decimal ml-5' } }),
      Document,
      // Paragraph.configure({ HTMLAttributes: { class: '' } }),
      Paragraph,
      Text,
      Link.configure({
        HTMLAttributes: { class: 'underline text-blue-600' },
        openOnClick: false,
      }),
      Bold,
      Italic,
      DropCursor,
    ],
    content: getContent(children),
    editable: false,
  });

  useEffect( () => {
    richTextView?.commands.setContent(getContent(children));
  }, [getContent, children] );

  return <EditorContent {...props} editor={richTextView}></EditorContent>;
}
